import test from 'ava';
import cards from './cards';
import { addCard } from '../actions/card';

test('cards reducer', (t) => {
	const actions = [addCard({ name: 'Ken', photo: 'poopybutthole.jpg' }), addCard({ name: 'Johee', photo: 'johee.jpg' })];
	const newState = actions.reduce(cards);
	t.deepEqual(newState, [{ id: 2, name: 'Johee', photo: 'johee.jpg' }, { id: 1, name: 'Ken', photo: 'poopybutthole.jpg' }]);
});
